.loader {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.loader>div {
  display: block;
  width: 100%;
  text-align: center;
}

.header-area {
  background-color: #eee;
}

.header {
  width: 940px;
  margin: 0 auto;
}

.brand a {
  text-decoration: none;
  font-size: 26px;
  color: #333;
}

.brand span {
  background-color: #333;
  color: #fff;
  padding: 0 8px;
  border-radius: 4px;
  margin-right: 5px;
}

.btn-add a {
  background-color: #4CAF50;
  color: #fff;
  text-decoration: none;
  padding: 5px 10px;
  border-radius: 4px;
}

.navbar ul {
  list-style: none;
  margin: 0;
  padding: 45px 0;
}

.navbar ul li {
  display: inline-block;
}

.navbar ul li.btn-add {
  float: right;
}

.header .intro {
  padding: 0 50px;
  text-align: center;
  padding-bottom: 100px;
}

.header .intro input {
  width: 100%;
  border: none;
  padding: 15px;
  border-radius: 4px;
  -webkit-box-shadow: 0 6px 8px rgba(102,119,136,.03), 0 1px 2px rgba(102,119,136,.3);
          box-shadow: 0 6px 8px rgba(102,119,136,.03), 0 1px 2px rgba(102,119,136,.3);
}

.header .intro p {
  margin-top: 25px;
  color: rgba(0, 0, 0, 0.6);
}

.top {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.top>div {
  -ms-flex-positive: 1;
      flex-grow: 1;
  width: calc(33% - 20px);
  margin: 10px;
  border-radius: 4px;
}

.container {
  width: 940px;
  margin: 0 auto;
  padding: 50px 0;
}

.mt-0 {
  margin-top: -50px;
}

.card {
  background: #fff;
  margin: 10px;
}

.c-card a {
  text-decoration: none;
  display: block;
  padding: 20px;
  color: #fff;
}

.contents h2 {
  text-align: center;
  font-size: 22px;
  color: rgba(0, 0, 0, 0.5);
}

.contents .c-card a {
  color: rgba(0, 0, 0, 0.75);
  border-radius: 4px;
  -webkit-box-shadow: 0 6px 8px rgba(102,119,136,.03), 0 1px 2px rgba(102,119,136,.3);
          box-shadow: 0 6px 8px rgba(102,119,136,.03), 0 1px 2px rgba(102,119,136,.3);
}

.contents .c-card h4 {
  font-size: 18px;
}

.items {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 30px;
}

.content h3 {
  font-size: 18px;
  padding-left: 15px;
  margin-bottom: 15px;
  color: rgba(0, 0, 0, 0.5);
}

.items>div {
  width: calc(33.3333% - 20px)
}

.c-card h4 {
  font-size: 22px;
  margin-bottom: 0;
}

.c-card p {
  margin-bottom: 0;
}

.single-content::after {
  content: "";
  display: block;
  clear: both;
}

.single-content>div {
  width: calc(50% - 30px);
  margin: 15px;
  float: left;
}

.single-items h3 {
  font-size: 18px;
  margin-bottom: 15px;
  color: rgba(0, 0, 0, 0.5);
}

.single-item {
  background-color: #fff;
  padding: 25px;
  border-radius: 4px;
  -webkit-box-shadow: 0 6px 8px rgba(102,119,136,.03), 0 1px 2px rgba(102,119,136,.3);
          box-shadow: 0 6px 8px rgba(102,119,136,.03), 0 1px 2px rgba(102,119,136,.3);
}

.single-item .item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 15px;
}

.single-item .item:last-child {
  margin-bottom: 0;
}

.single-item .item>* {
  -ms-flex: 1 1 50%;
      flex: 1 1 50%;
}

.single-item .code,
.single-item p {
  margin-bottom: 0;
}

.single-item pre {
  padding: 5px;
  background-color: transparent;
  color: #1565C0;
}

footer ul {
  list-style: none;
  margin-bottom: 10px;
}

footer ul li {
  display: inline-block;
  margin: 0 5px;
}

footer ul li a {
  background: #333;
  color: #fff;
  text-decoration: none;
  padding: 2px 10px;
  font-weight: 900;
  text-transform: uppercase;
  border-radius: 4px;
  font-size: 13px;
}

.footer-area {
  background-color: #eee;
  text-align: center;
}



